/* TO-DO this entire styling must be part with styled component */

input[type="range"] {
  background: #0198ff;
}

input[type="range"] {
  -webkit-appearance: none;
  margin: 18px 0;
  width: 100%;
}

input[type="range"]:focus {
  outline: none;
}

input[type="range"]::-webkit-slider-runnable-track {
  width: 100%;
  height: 3px;
  cursor: pointer;
  animate: 0.2s;
  background: #ffffff;
  border-radius: 30%;
  border: 0.2px solid #010101;
}

input[type="range"]::-webkit-slider-thumb {
  border: 1px solid #000000;
  height: 16px;
  border-radius: 50%;
  width: 16px;
  background: #ffffff;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -6.7px;
}

input[type="range"]:focus::-webkit-slider-runnable-track {
  background: #ffffff;
  opacity: 0.5;
}

input[type="range"]::-moz-range-track {
  width: 100%;
  height: 3px;
  cursor: pointer;
  animate: 0.2s;
  background: #3071a9;
  border-radius: 30%;
  border: 0.2px solid #010101;
}

input[type="range"]::-moz-range-thumb {
  border: 1px solid #000000;
  height: 16px;
  border-radius: 50%;
  width: 16px;
  background: #ffffff;
  cursor: pointer;
}

input[type="range"]::-ms-track {
  width: 100%;
  height: 3px;
  cursor: pointer;
  animate: 0.2s;
  background: transparent;
  border-color: transparent;
  border-width: 16px 0;
  color: transparent;
}

input[type="range"]::-ms-fill-lower {
  background: #2a6495;
  border: 0.2px solid #010101;
  border-radius: 2.6px;
}

input[type="range"]::-ms-fill-upper {
  background: #3071a9;
  border: 0.2px solid #010101;
  border-radius: 2.6px;
}

input[type="range"]::-ms-thumb {
  border: 1px solid #000000;
  height: 16px;
  border-radius: 50%;
  width: 16px;
  background: #ffffff;
  cursor: pointer;
}

input[type="range"]:focus::-ms-fill-lower {
  background: #3071a9;
}

input[type="range"]:focus::-ms-fill-upper {
  background: #ffffff;
  opacity: 0.5;
}

body {
  margin: 0;
  padding: 0;
}

.Apps {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

/*.crop-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 80px;
}*/

.App {
  display: grid;
  grid-template-rows: 1fr 60px 80px;
  height: 90%;
  grid-row-gap: 30px;
  row-gap: 30px;
}

/* this media query must be relocated on future */
@media (max-width: 900px) {
  .App {
    grid-template-rows: 1fr 60px 130px;
  }
}

.crop-container {
  display: flex;
  flex-direction: column-reverse;
  width: 100%;
  height: 100%;
  position: relative;
  align-self: center;
}

.ImgCropcontrols {
  margin: auto;
  width: 100%;
  height: 80px;
  display: flex;
  align-items: center;
  background: -webkit-gradient(linear, left top, left bottom, from(transparent), to(black));
  background: linear-gradient(to bottom, transparent, black);
  justify-content: center;
}

.slider {
  width: 200px;
  height: 10px;
}

.crop-controls svg {
  cursor: pointer;
}

.crop-controls {
  display: grid;
  background: #ffffff;
  border-radius: 4px;
  grid-template-columns: 1fr auto;
  align-items: center;
  padding: 0 30px;
  grid-column-gap: 15px;
  -webkit-column-gap: 15px;
          column-gap: 15px;
  max-width: 700px;
  justify-self: center;
  width: 100%;
}

.crop-controls div:nth-child(1) {
  width: auto;
  display: grid;
  grid-template-columns: auto 1fr auto;
  align-items: center;
  grid-column-gap: 10px;
  -webkit-column-gap: 10px;
          column-gap: 10px;
}

.crop-controls .crop-rotate-button {
  color: gray;
  background: transparent;
  border: none;
  font-size: 20px;
  margin-top: 6px;
  width: 60px;
  height: 30px;
  cursor: pointer;
  text-align: center;
  line-height: 30px;
}

